/* CSS Custom Properties for theming */
:root {
  /* Dark theme (default) */
  --network-bg-primary: rgba(15, 20, 25, 0.95);
  --network-bg-secondary: rgba(255, 255, 255, 0.02);
  --network-bg-tertiary: rgba(15, 20, 25, 0.9);
  --network-text-primary: #ffffff;
  --network-text-secondary: #b0b0b0;
  --network-text-tertiary: #a0a0a0;
  --network-text-accent: #10b981;
  --network-border-primary: rgba(255, 255, 255, 0.1);
  --network-border-secondary: rgba(255, 255, 255, 0.3);
  --network-shadow: rgba(0, 0, 0, 0.3);
  --network-hover-bg: rgba(255, 255, 255, 0.05);
  --network-node-stroke: #ffffff;
  --network-text-shadow: rgba(0, 0, 0, 0.8);
  --network-link-color: #4ade80;
  --network-link-hover: #22c55e;
  --network-funding-color: #fbbf24;
  --network-close-color: #666;
  --network-close-hover: #ffffff;
  --network-close-hover-bg: rgba(255, 255, 255, 0.1);
}

/* Light theme */
[data-theme="light"] {
  --network-bg-primary: rgba(255, 255, 255, 0.95);
  --network-bg-secondary: rgba(0, 0, 0, 0.02);
  --network-bg-tertiary: rgba(255, 255, 255, 0.9);
  --network-text-primary: #1a202c;
  --network-text-secondary: #4a5568;
  --network-text-tertiary: #718096;
  --network-text-accent: #059669;
  --network-border-primary: rgba(0, 0, 0, 0.1);
  --network-border-secondary: rgba(0, 0, 0, 0.3);
  --network-shadow: rgba(0, 0, 0, 0.1);
  --network-hover-bg: rgba(0, 0, 0, 0.05);
  --network-node-stroke: #333333;
  --network-text-shadow: rgba(255, 255, 255, 0.8);
  --network-link-color: #059669;
  --network-link-hover: #047857;
  --network-funding-color: #d97706;
  --network-close-color: #666;
  --network-close-hover: #1a202c;
  --network-close-hover-bg: rgba(0, 0, 0, 0.1);
}

.network-visualization {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Network Header */
.network-header {
  background: var(--network-bg-primary);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--network-border-primary);
  padding: 20px;
  text-align: center;
  z-index: 20;
  transition: all 0.3s ease;
}

.network-header h2 {
  color: var(--network-text-accent);
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 12px;
  margin-top: 0;
  transition: color 0.3s ease;
}

.network-header p {
  color: var(--network-text-secondary);
  font-size: 1.1rem;
  line-height: 1.5;
  margin: 0;
  max-width: 1000px;
  margin: 0 auto;
  text-align: justify;
  transition: color 0.3s ease;
}

/* Main Container */
.network-main {
  display: flex;
  flex: 1;
  overflow: hidden;
}

/* Left Sidebar - Filters */
.network-filters {
  position: relative;
  width: 250px;
  height: 100%;
  background: var(--network-bg-primary);
  backdrop-filter: blur(10px);
  border-right: 1px solid var(--network-border-primary);
  padding: 20px;
  z-index: 10;
  overflow-y: auto;
  transition: all 0.3s ease;
}

.network-filters h4 {
  color: var(--network-text-primary);
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 16px;
  margin-top: 0;
  transition: color 0.3s ease;
}

.filter-checkboxes {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.filter-checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  padding: 8px 0;
  transition: all 0.2s ease;
}

.filter-checkbox:hover {
  background: var(--network-hover-bg);
  border-radius: 6px;
  padding: 8px 8px;
  margin: 0 -8px;
}

.filter-checkbox input[type="checkbox"] {
  display: none;
}

.checkbox-custom {
  width: 18px;
  height: 18px;
  border: 2px solid var(--network-border-secondary);
  border-radius: 4px;
  background: transparent;
  position: relative;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.filter-checkbox input[type="checkbox"]:checked + .checkbox-custom {
  background: var(--network-text-accent);
  border-color: var(--network-text-accent);
}

.filter-checkbox input[type="checkbox"]:checked + .checkbox-custom::after {
  content: '✓';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 12px;
  font-weight: bold;
}

.checkbox-label {
  color: var(--network-text-tertiary);
  font-size: 0.9rem;
  font-weight: 500;
  transition: color 0.2s ease;
}

.filter-checkbox:hover .checkbox-label {
  color: var(--network-text-primary);
}

.filter-checkbox input[type="checkbox"]:checked + .checkbox-custom + .checkbox-label {
  color: var(--network-text-accent);
  font-weight: 600;
}

/* Main Network Canvas */
.network-canvas {
  flex: 1;
  height: 100%;
  position: relative;
  background: var(--network-bg-secondary);
  overflow: hidden;
  transition: background 0.3s ease;
}

.network-canvas svg {
  background: transparent;
  width: 100%;
  height: 100%;
}

/* Enhanced label styling for better readability */
.network-canvas text {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Improved node visibility with enhanced shadows */
.network-canvas circle {
  filter: drop-shadow(0 2px 6px var(--network-shadow));
  transition: filter 0.2s ease;
}

.network-canvas circle:hover {
  filter: drop-shadow(0 4px 12px var(--network-shadow));
}

/* Enhanced link visibility */
.network-canvas line {
  transition: stroke-opacity 0.2s ease;
}

/* Right Sidebar - Legend */
.network-legend {
  position: relative;
  width: 280px;
  height: 100%;
  background: var(--network-bg-primary);
  backdrop-filter: blur(10px);
  border-left: 1px solid var(--network-border-primary);
  padding: 20px;
  z-index: 10;
  overflow-y: auto;
  box-shadow: 0 8px 32px var(--network-shadow);
  transition: all 0.3s ease;
}

.network-legend h4 {
  color: var(--network-text-primary);
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 16px;
  margin-top: 0;
  transition: color 0.3s ease;
}

.legend-section {
  margin-bottom: 20px;
}

.legend-section h5 {
  color: var(--network-text-accent);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 10px;
  font-weight: 600;
  transition: color 0.3s ease;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 8px;
}

.legend-color {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 2px solid var(--network-text-primary);
  flex-shrink: 0;
  transition: border-color 0.3s ease;
}

.legend-line {
  width: 24px;
  height: 2px;
  border-top: 2px solid;
  flex-shrink: 0;
}

.legend-label {
  color: var(--network-text-tertiary);
  font-size: 0.9rem;
  font-weight: 500;
  transition: color 0.3s ease;
}

/* Loading and Performance States */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--network-bg-tertiary);
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 100;
  transition: background 0.3s ease;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top: 3px solid var(--network-text-accent);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
  transition: border-top-color 0.3s ease;
}

.loading-overlay p {
  color: var(--network-text-primary);
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
  transition: color 0.3s ease;
}

.processing-indicator {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--network-bg-tertiary);
  backdrop-filter: blur(10px);
  border: 1px solid var(--network-border-primary);
  border-radius: 8px;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  z-index: 50;
  transition: all 0.3s ease;
}

.processing-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-top: 2px solid var(--network-text-accent);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  transition: border-top-color 0.3s ease;
}

.processing-indicator span {
  color: var(--network-text-primary);
  font-size: 1.1rem;
  font-weight: 500;
  transition: color 0.3s ease;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Tooltips */
.node-tooltip {
  position: absolute;
  bottom: 20px;
  left: 270px; /* Positioned after left sidebar */
  background: var(--network-bg-tertiary);
  backdrop-filter: blur(10px);
  border: 1px solid var(--network-border-primary);
  border-radius: 8px;
  padding: 16px;
  max-width: 350px;
  z-index: 100;
  animation: fadeIn 0.3s ease;
  box-shadow: 0 8px 32px var(--network-shadow);
  transition: all 0.3s ease;
}

.tooltip-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.tooltip-header h3,
.tooltip-header h4 {
  margin: 0;
  flex: 1;
}

.tooltip-close {
  background: none;
  border: none;
  color: var(--network-close-color);
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  padding: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s ease;
  margin-left: 8px;
}

.tooltip-close:hover {
  background: var(--network-close-hover-bg);
  color: var(--network-close-hover);
}

.node-tooltip h3 {
  color: var(--network-text-primary);
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 8px;
  transition: color 0.3s ease;
}

.node-type {
  color: var(--network-text-accent);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 8px;
  font-weight: 500;
  transition: color 0.3s ease;
}

.node-description {
  color: var(--network-text-secondary);
  font-size: 0.9rem;
  line-height: 1.4;
  transition: color 0.3s ease;
}

.tooltip-section {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid var(--network-border-primary);
  transition: border-color 0.3s ease;
}

.tooltip-section h4 {
  color: var(--network-text-accent);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 6px;
  font-weight: 600;
  transition: color 0.3s ease;
}

.website-link {
  color: var(--network-link-color);
  text-decoration: none;
  font-size: 0.9rem;
  word-break: break-all;
  transition: color 0.2s ease;
}

.website-link:hover {
  color: var(--network-link-hover);
  text-decoration: underline;
}

.personnel-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.personnel-list li {
  color: var(--network-text-secondary);
  font-size: 0.9rem;
  padding: 2px 0;
  position: relative;
  padding-left: 12px;
  transition: color 0.3s ease;
}

.personnel-list li:before {
  content: "•";
  color: var(--network-text-accent);
  position: absolute;
  left: 0;
  transition: color 0.3s ease;
}

.funding-info {
  color: var(--network-funding-color);
  font-size: 0.9rem;
  font-weight: 500;
  margin: 0;
  transition: color 0.3s ease;
}

.recent-news {
  color: var(--network-text-secondary);
  font-size: 0.9rem;
  line-height: 1.4;
  margin: 0;
  font-style: italic;
  transition: color 0.3s ease;
}

.link-tooltip {
  position: absolute;
  bottom: 20px;
  right: 300px; /* Positioned before right sidebar */
  background: var(--network-bg-tertiary);
  backdrop-filter: blur(10px);
  border: 1px solid var(--network-border-primary);
  border-radius: 8px;
  padding: 16px;
  max-width: 350px;
  z-index: 100;
  animation: fadeIn 0.3s ease;
  box-shadow: 0 8px 32px var(--network-shadow);
  transition: all 0.3s ease;
}

.link-tooltip h4 {
  color: var(--network-text-primary);
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 8px;
  transition: color 0.3s ease;
}

.link-type {
  color: var(--network-text-accent);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 8px;
  font-weight: 500;
  transition: color 0.3s ease;
}

.link-description {
  color: var(--network-text-secondary);
  font-size: 0.9rem;
  line-height: 1.4;
  margin-bottom: 8px;
  transition: color 0.3s ease;
}

.link-strength {
  color: var(--network-funding-color);
  font-size: 0.9rem;
  font-weight: 500;
  transition: color 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .network-visualization {
    height: auto;
  }
  
  .network-header {
    padding: 15px;
  }
  
  .network-header h2 {
    font-size: 1.8rem;
  }
  
  .network-header p {
    font-size: 1.1rem;
  }
  
  .network-main {
    flex-direction: column;
  }
  
  .network-filters {
    width: 100%;
    height: auto;
    border-right: none;
    border-bottom: 1px solid var(--network-border-primary);
  }
  
  .network-canvas {
    height: 60vh;
  }
  
  .network-legend {
    width: 100%;
    height: auto;
    border-left: none;
    border-top: 1px solid var(--network-border-primary);
  }
  
  .processing-indicator {
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .node-tooltip {
    left: 20px;
    max-width: calc(100vw - 40px);
  }
  
  .link-tooltip {
    right: 20px;
    max-width: calc(100vw - 40px);
  }
} 